body {
    margin: 0;
    font-family: 'Kanit-Medium';
}

body, html {
    height: 100%;
}

#glamping .parallax {
    background-image: url(/couple-travels/static/media/Parallax.d6cb718b.png);
    min-height: 1000px;
    height: 80%;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

#glamping .banner {
    position: relative;
}

#glamping .banner-image {
    min-height: 500px;
    background-image: url(/couple-travels/static/media/Banner.4d4581da.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    -webkit-filter: brightness(0.3);
            filter: brightness(0.3);
}

#glamping .banner-square {
    position: fixed;
    top: 100px;
    left: 0;
    min-width: 300px;
    padding: 30px 20px;
    background-color: #efefef;
    z-index: 10;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    box-shadow: 0px 0px 200px #aadfe3;
    -webkit-clip-path: polygon(90% 0%, 100% 50%, 90% 100%, 0% 100%, 0% 50%, 0% 0%);
            clip-path: polygon(90% 0%, 100% 50%, 90% 100%, 0% 100%, 0% 50%, 0% 0%);
    transition: left 1s ease-out 0s, position 1s ease-out 0s;
}

#glamping .banner-square.initial {
    position: absolute;
    top: 250px;
    left: 200px;
}

#glamping .banner-text {
    position: relative;
}

#glamping .banner-text h1 {
    font-family: "DancingScript-Regular";
    font-size: 35px;
    color: black;
    font-weight: bolder;
    letter-spacing: 5px;
    margin: 0px;
}

#glamping h2 {
    font-family: "DancingScript-Regular";
    font-size: 30px;
    color: black;
    font-weight: bolder;
    letter-spacing: 5px;
    margin: 0px;
    text-transform: capitalize;
    margin-bottom: 40px;
}

#glamping .main-description-container {
    background-color: #F09D85;
    padding: 30px 0;
}

#glamping .main-description {
    text-align: center;
    background-color: #F09D85;
    min-height: 350px;
    margin: 100px 25% 50px;
}
#glamping .main-description p {
    font-family: 'Kanit-Medium';
    font-size: 20px;
}
#glamping .main-description h2 span {
    position: relative;
}


#glamping .main-description h2 span::after {
    content: "";
    width: 35px;
    height: 3px;
    background-color: black;
    position: absolute;
    bottom: 0;
    left: -2px;
}

#glamping .main-description p span {
    font-family: 'Kanit-Medium';
    font-size: 22px;
    font-weight: bolder;
}

#glamping .main-description ol span {
    position: relative;
}

#glamping .main-description ol {
    list-style: none;
}
#glamping .main-description ol li {
    display: flex;
    justify-content: center;
}

#glamping .main-description ol li p {
    position: relative;
    font-size: 16px;
}

#glamping .main-description ol li p span {
    position: relative;
    font-size: 16px;
    font-weight: normal;
}

#glamping .main-description ol li p::before {
    content: "";
    width: 3px;
    height: 3px;
    background-color: black;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: -10px;
}

#glamping .main-description ol span::after {
    content: "";
    width: 66px;
    height: 1px;
    background-color: black;
    position: absolute;
    top: 55%;
    left: -2px;
    -webkit-transform: rotate(-3deg);
            transform: rotate(-3deg);
}

#glamping .line {
    position: absolute;
    width: 15px;
    height: 5px;
    -webkit-transform: rotate(100deg) translateY(-50%);
            transform: rotate(100deg) translateY(-50%);
    background-color: black;
    -webkit-clip-path: polygon(0 20%, 100% 0, 100% 100%, 0 90%);
            clip-path: polygon(0 20%, 100% 0, 100% 100%, 0 90%);
}

#glamping .first-line {
    bottom: -33px;
    right: 30px;
}

#glamping .second-line {
    bottom: -33px;
    right: 40px;
}

#glamping .countdown-container {
    background-color: #fcd2a4;
    display: flex;
    justify-content: center;
    padding: 50px 0;
    flex-wrap: wrap;
}

#glamping .countdown-container h2 {
    width: 100%;
    text-align: center;
}

#glamping .countdown-container ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: nowrap;

}

#glamping .countdown-container ul li {
    margin: 0 30px;
    
}

#glamping .countdown-container ul li .number {
    background-color: #f7b36a;
    width: 30px;
    height: 30px;
    color: black;
    margin: auto;
    font-weight: bolder;
    font-size: 20px;
    text-align: center;
    padding: 10px;
    font-family: 'Kanit-Medium';
}

#glamping .countdown-container ul li .label {
    font-family: 'Kanit-Light';
    font-weight: bolder;
    text-align: center;
}

#glamping .activities {
    padding: 50px;
    background-color: #F09D85;
}

#glamping .activities > p {
    text-align: center;
}

#glamping .activities h2 {
    text-align: center;
}

#glamping .activities ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

#glamping .activities ul li {
    display: flex;
    justify-content: center;
    margin-bottom: 15px;
}

#glamping .activities ul li:nth-child(2n) {
    flex-direction: row-reverse;
}


#glamping .activities ul li:nth-child(2n) .description {
    border-left: solid 1px black;
}
#glamping .activities ul li:nth-child(2n-1) .description {
    border-right: solid 1px black;
}

#glamping .activities .description p {
    margin: 0;
}
#glamping .activities .description h3 {
    margin: 0;
}

#glamping .activities p.hour {
    font-family: 'Kanit-ExtraLight';
    font-size: 14px;
    letter-spacing: 1px;
}

#glamping .activities .description {
    max-width: 200px;
    min-width: 200px;
    color: white;
    font-family: 'Kanit-Light';
    padding-top: 15px;
    padding-bottom: 15px;
}

#glamping .kayak .description {
    padding-left: 35px;
}

#glamping .kayak img {
    width: 130px;
    height: 100%;
    margin: auto 80px auto 25px;
}

#glamping .bonfire .description {
    padding-right: 35px;
}

#glamping .bonfire img  {
    width: 70px;
    height: 100%;
    margin: auto 115px auto 50px;
}

#glamping .arrive .description {
    padding-left: 35px;
}

#glamping .arrive img  {
    width: 130px;
    height: 100%;
    margin: auto 80px auto 25px;
}


#glamping .penhol .description {
    padding-right: 35px;
}

#glamping .penhol img  {
    width: 150px;
    height: 100%;
    margin: auto 45px auto 40px;
}

#glamping .mimir .description {
    padding-right: 35px;
}

#glamping .mimir img  {
    width: 100px;
    height: 100%;
    margin: auto 75px auto 60px;
    -webkit-transform: rotate(20deg);
            transform: rotate(20deg);
}

#glamping .hiking .description {
    padding-right: 35px;
}

#glamping .hiking img  {
    width: 100px;
    height: 100%;
    margin: auto 75px auto 60px;
}

#glamping .peach .description {
    padding-left: 35px;
}

#glamping .peach img  {
    width: 100px;
    height: 100%;
    margin: auto 75px auto 60px;
}

#glamping .movie .description {
    padding-left: 35px;
}

#glamping .movie img {
    width: 80px;
    height: 100%;
    margin: auto 80px auto 75px;
}


#glamping .trip-start {
    background-color: white;
    display: grid;
    grid-template-columns: 75% 25%;
}
#glamping .trip-start .trip-text {
    margin: 50px 200px 0px 100px;
}

#glamping .trip-start .trip-image {
    width: 100%;
    height: auto;
    -webkit-filter: blur(0.5px);
            filter: blur(0.5px);
}


#glamping .pocket-pain {
    display: flex;
    text-align: center;
    position: relative;
}
#glamping .pocket-pain .glamping-prices {
    width: 50%;
    background-color: #fcd2a4;
    margin: auto;
    padding-top: 40px;
    padding-bottom: 40px;
}
#glamping .pocket-pain .bus-prices {
    width: 50%;
    background-color: #f7b36a;
    margin: auto;
    padding-top: 40px;
    padding-bottom: 40px;
}

#glamping .pocket-pain .pocket-pain-img {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 100px;
    height: 100px;
    -webkit-clip-path: circle(49% at 50% 50%);
            clip-path: circle(49% at 50% 50%);
    background: rgb(247,179,106);
    background: linear-gradient(90deg, rgba(247,179,106,1) 0%, rgba(252,210,164,1) 100%);
}


#roller-skate {
    overflow: hidden;
    background-color: #040126;
}

.roller-skate-image {
    height: 261px !important;
    width: 500px !important;
    margin-left: -80px;
}


#roller-skate .roller-header {
    background-image: url(/couple-travels/static/media/background.12c32801.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 500px;
    position: relative;
    overflow: hidden;
}

#roller-skate .roller-header::before {
    content: " ";
    width: 100%;
    height: 100%;
    position: absolute;
    background-image: url(/couple-travels/static/media/header-lines.50a42d01.png);
    top: -30px;
    right: -20px;
    background-repeat: no-repeat;
    background-position: top;
    background-size: contain;
}



#roller-skate .roller-header h1 {
    color: white;
    margin: 0;
    padding-top: 250px;
    font-size: 120px;
    letter-spacing: 5px;
}


#roller-skate h2 {
    color: #ed246c;
    margin: 0;
    font-size: 35px;
    letter-spacing: 3px;
}

#roller-skate .roller-header h2 {
    padding-bottom: 300px;
}

#roller-skate .countdown-container {
    position: relative;
    margin-top: -170px;
}

#roller-skate .countdown-container ul {
    list-style: none;
}

#roller-skate .countdown-container ul li {
    display: flex;
    justify-content: center;
}

#roller-skate .outer-border {
    margin: 10px;
    border: solid 10px #ed246c;
}

#roller-skate .countdown-container .number, #roller-skate .countdown-container .label {
    color: white;
    padding: 0px 30px;
    border: solid 1px white;
    background-color: #ed246c;
    font-size: 100px;
    text-transform: uppercase;
}


#roller-skate .countdown-container::before {
    content: " ";
    background-color: #ed246c;
    width: 300px;
    height: 300px;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: rotate(45deg) translate(-93px,122px);
            transform: rotate(45deg) translate(-93px,122px); 
}

#roller-skate .countdown-container::after {
    content: " ";
    background-color: #ed246c;
    width: 300px;
    height: 300px;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: rotate(45deg) translate(122px,-93px);
            transform: rotate(45deg) translate(122px,-93px);
}

#roller-skate .roller-description {
    color: white;
    font-size: 40px;
    margin: 0 10%;
}


#roller-skate .roller-images {
    background-image: url(/couple-travels/static/media/photo-bg.1f0970d8.jpg);
    margin: 100px auto;
    padding: 60px;
    width: 600px;
    border-radius: 20px;
}

#roller-skate .roller-skate-image {
    border: 1px solid white;
}

#roller-skate .star-container {
    position: relative;
}

#roller-skate .star {
    position: absolute;
    width: 90px;
    height: 90px;
    background-image: url(data:image/png;base64,);
    background-size: cover;
}

#roller-skate .star.one {
    top: 10px;
    left: 150px;
}

#roller-skate .star.two {
    top: 250px;
    left: 300px;
}

#roller-skate .star.three {
    top: 400px;
    left: 100px;
}

#roller-skate .star.four {
    top: 10px;
    right: 300px;
}

#roller-skate .star.five {
    top: 250px;
    right: 150px;
}

#roller-skate .star.six {
    top: 400px;
    right: 300px;
}
